@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.wrapper {
background: #858383;
width: 20rem;
padding: 2rem;
margin-top: 4rem;
border-radius: 1rem;
box-shadow: 0px 9px 15px -3px rgba(0,0,0,0.1)
}
.screen {
  margin-bottom: 1.5rem;
  background: #f79505;
  border-radius: 1.5rem;
}
.spanStyle {
  width: 1rem;
  background: #242424;
  height: 1rem;
  border-radius: 50%;
  margin-bottom: 0.5rem;
}
.spanStyle::after {
  content: '';
  display: block;
  width: 1rem;
  background: #242424;
  height: 1rem;
  border-radius: 50%;
  margin-bottom: 0.5rem;
  margin-left: 1.5rem;
}
body {
  height: 100vh;
  background: #f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto';
}
.buttonBox {
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: .5rem;
}
.button {
  background: #e9f0f4;
  height: 3.5rem;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #242424;
  border: none;
  font-size: 1.5rem;
}
.button:hover {
  border: 2px dotted #242424;
}
.equals {
  background: #4bd086;
  grid-column: 3/5;
}
.opt {
  background: #f79505;
}